<template>
  <div class="content-wrapper bg-hui h-100">
    <div class="row">
      <div class="col-sm-1"></div>
      <div class="col-sm-10 user-bat-wrapper text-center ">
        

        <h1 class="bigger-title">
          {{ introduce }}
        </h1>
        <h1>
          {{ named }}
        </h1>

        <h4 class="mt-4 pt-4">
          跳轉至：
        </h4>
        <div 
          v-for="(v, i) in items" :key="i"
          class="user-bar hand mb-3 " 

          @click="go(v.link)"
        >
          <div class="ub-wrapper fl ">
            <div class="ub-face f-face flex-center"
            
            :class="'ub-fs-' + v.size"
            >
              {{ v.named }}
            </div>
          </div>
          <div class="ub-wrapper fr  f-icon flex-center"
          >
            <i class="fa fa-angle-double-right fa-face-fix"></i>
          </div>
        </div>
      </div>
      <div class="col-sm-1"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Dashboard',
  data() {
    return {
      items: [
        {
          named: '發票',
          link: '/home/listing',
          size: 1
        },
        {
          named: '報價單',
          link: '/home/prices',
          size: 2
        }
      ]
    }
  },
  computed: {
    named() {
      const n = this.$store.getters.getUserName()
      if (!n) {
        return '未登錄'
      }
      return n
    },
    introduce() {
      const n = this.$store.getters.getUserName()
      if (!n) {
        return '系統監測到您，'
      }
      return '歡迎，'
    }
  },
  methods: {
    go(i) {
      this.$router.push(i)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.panel-a, .panel-b
  height: 42vh
  min-height: 42vh
  margin-bottom: 1vh
  padding: $horizon

.panel-c
  height: 85vh
  min-height: 85vh
  margin-bottom: $horizonS
  padding: $horizon

.bigger-title
  font-size: 3.6vw

.user-bat-wrapper
  padding-top: 20vh
  padding-bottom: 2vh

.user-bar
  width: 360px
  margin: 0 auto
  background: #fbfbfb
  border-radius: 75px
  min-height: 60px

  display: flex
  justify-content: space-between

  box-shadow: 1px 2px 5px -1px $shadow

.ub-wrapper
  border-radius: 200px
  display: inline-block
  width: calc( 46px + 2vw )
  height: calc( 46px + 2vw )

.ub-fs-1
  font-size: 26px !important
.ub-fs-2
  font-size: 22px !important

.f-face
  font-size: 26px

.f-icon
  font-size: 32px

.fa-face-fix
  padding-bottom: 7px

.ub-face
  background: $skeletonInputWait
  width: 100% 
  height: 100%

</style>


